@import url(https://fonts.googleapis.com/css2?family=Architects+Daughter&display=swap);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Architects Daughter', cursive;
    color: cyan;
}




.home {
    min-height: 100vh;
    background: url(/static/media/wallpaper.553bde5c.jpg);
    background-size: cover;
    background-color: rgba(0, 0, 0, 0.6);
    background-blend-mode: multiply;
    display: grid;
    place-items: center;
    color: white;
    text-shadow: 2px 4px 3px rgba(0, 0, 0, 0.3);
}

.avatar-wrapper {
    border-radius: 50%;
    width: 20em;
    box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px,
        rgba(0, 0, 0, 0.22) 0px 15px 12px;
}

.avatar-wrapper img {
    border-radius: inherit;
    width: inherit;
}

.hover:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    transition: -webkit-transform 0.2s;
    transition: transform 0.2s;
    transition: transform 0.2s, -webkit-transform 0.2s;
}

.home-title {
    font-size: 2em;
    font-weight: 600;
    text-shadow: 2px 4px 3px rgba(0, 0, 0, 0.3);
}

.avatar-name {
    color: yellow;
}

.home-information {
    display: flex;
    flex-direction: column;
    text-align: center;
    grid-gap: 1em;
    gap: 1em;
}

.typewriter-text {
    font-weight: bold;
    font-size: 0.9em;
}

.home-desc {
    text-align: center;
    font-size: 2em;
}

@media (max-width: 550px) {
    .typewriter-text {
        font-size: 0.8em;
    }
}

/* Waving Hand https://codepen.io/jakejarvis/pen/pBZWZw */

.wave {
    -webkit-animation-name: wave-animation;
            animation-name: wave-animation; /* Refers to the name of your @keyframes element below */
    -webkit-animation-duration: 2.5s;
            animation-duration: 2.5s; /* Change to speed up or slow down */
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite; /* Never stop waving :) */
    -webkit-transform-origin: 70% 70%;
            transform-origin: 70% 70%; /* Pivot around the bottom-left palm */
    display: inline-block;
}

.home-social-links {
    display: flex;
    grid-gap: 1em;
    gap: 1em;
    justify-content: center;
}

.social-media-link:hover {
    -webkit-transform: scale(1.5) rotate(360deg);
            transform: scale(1.5) rotate(360deg);
    transition: all 0.5s;
    cursor: pointer;
}

@-webkit-keyframes wave-animation {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    10% {
        -webkit-transform: rotate(14deg);
                transform: rotate(14deg);
    }
    20% {
        -webkit-transform: rotate(-8deg);
                transform: rotate(-8deg);
    }
    30% {
        -webkit-transform: rotate(14deg);
                transform: rotate(14deg);
    }
    40% {
        -webkit-transform: rotate(-4deg);
                transform: rotate(-4deg);
    }
    50% {
        -webkit-transform: rotate(10deg);
                transform: rotate(10deg);
    }
    60% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
}

@keyframes wave-animation {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    10% {
        -webkit-transform: rotate(14deg);
                transform: rotate(14deg);
    }
    20% {
        -webkit-transform: rotate(-8deg);
                transform: rotate(-8deg);
    }
    30% {
        -webkit-transform: rotate(14deg);
                transform: rotate(14deg);
    }
    40% {
        -webkit-transform: rotate(-4deg);
                transform: rotate(-4deg);
    }
    50% {
        -webkit-transform: rotate(10deg);
                transform: rotate(10deg);
    }
    60% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
}

.projects {
    background: url(/static/media/wallpaper3.6bb134cd.jpg);
    min-height: 100vh;
    background-size: cover;
    background-color: rgba(0, 0, 0, 0.6);
    background-blend-mode: multiply;
    display: grid;
    place-items: center;
    color: white;
    text-shadow: 2px 4px 3px rgba(0, 0, 0, 0.3);
}

.about {
    background: url(/static/media/wallpaper2.9928807a.jpg);
    min-height: 200vh;
    background-size: cover;
    background-color: rgba(0, 0, 0, 0.6);
    background-blend-mode: multiply;
    background-position: center;
    display: grid;
    place-items: center;
    color: white;
    text-shadow: 2px 4px 3px rgba(0, 0, 0, 0.3);
}

.about-information {
    display: flex;
    justify-content: center;
    align-items: center;
    grid-gap: 5em;
    gap: 5em;
    width: 100%;
}
.card-information,
.skills-information {
    display: flex;
    flex-direction: column;
    grid-gap: 1em;
    gap: 1em;
    width: 60vh;
}

.skills-information {
    width: 100vh;
}

.card-title {
    text-align: center;
    font-size: 2em;
}

.card-desc {
    font-size: 1.25em;
}

.card-hobbies {
    padding-left: 3em;
}

.skill-cards {
    display: grid;
    grid-gap: 2em;
    gap: 2em;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
}

.skill-card {
    border: 2px solid rgb(150, 35, 165);
    font-size: 5em;
    padding: 0.25em;
    width: 2em;
}

.skills-scroll {
    text-align: center;
    font-weight: bold;
    color: rgb(207, 73, 225);
    cursor: pointer;
}

.skill-card:hover {
    border: 2px solid rgb(151, 53, 164);
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    transition: all 0.5s ease;
}

@media (max-width: 550px) {
    .card-information,
    .skills-information {
        width: 40vh;
    }

    .skill-cards {
        grid-template-columns: repeat(2, 1fr);
    }

    .about {
        height: 220vh;
    }
}

svg {
    height: auto;
    width: auto;
}

.navbar {
    display: flex;
    position: fixed;
    justify-content: space-between;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.3);
    padding: 0.5em;
    box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px,
        rgba(0, 0, 0, 0.23) 0px 6px 6px;
    width: 100%;
    font-weight: bold;
    text-shadow: 2px 4px 3px rgba(0, 0, 0, 0.3);
    font-size: 1.2em;
}

.navbar-button,
.hamburger-button {
    display: flex;
    align-items: center;
    grid-gap: 0.5em;
    gap: 0.5em;
    color: white;
    -webkit-user-select: none;
            user-select: none;
    cursor: pointer;
}

.un {
    padding-bottom: 5px;
    background-image: linear-gradient(rgb(0, 255, 72) 0 0);
    background-position: 0 100%; /*OR bottom left*/
    background-size: 0% 5px;
    background-repeat: no-repeat;
    transition: background-size 0.4s, background-position 0s 0.4s; /*change after the size immediately*/
}

.un:hover {
    background-position: 100% 100%; /*OR bottom right*/
    background-size: 100% 5px;
}

.navbar-links {
    display: flex;
    grid-gap: 2em;
    gap: 2em;
}

.navbar-name {
    padding-left: 1em;
}

.MuiDrawer-paper {
    background-color: rgba(0, 0, 0, 0.8) !important;
}
.nav-expand-less {
    display: flex;
    align-self: flex-end;
    padding: 0.5em;
}

.empty-icon {
    width: 40px;
    height: 40px;
}

